.favoriteButton {
  pointer-events: all;
  display: block;
  background-color: transparent;
  background-position: center;
  background-size: contain;
  position: absolute;
  bottom: 0;
  right: min(15vw, 15vh);
  width: min(15vw, 15vh);
  height: min(15vw, 15vh);
  padding: 0;
}

.favoriteButton.mobile {
  bottom: calc(max(8vw, 8vh) / 2);
  right: calc(max(8vw, 8vh) * 1.75);
  width: max(8vw, 8vh);
  height: max(8vw, 8vh);
}